// router
function router() {
  const routePath = location.hash;

  if(routePath === '') {
    // Í∏Ä Î™©Î°ù, # Í∞íÏù¥ Îì§Ïñ¥Ïò® Í≤ΩÏö∞ ÎπàÍ∞íÏúºÎ°ú ÌåêÎã®Ìï®
    newsFeed();
  } else {
    // Í∏Ä ÏÑ∏Î∂Ä ÎÇ¥Ïö©
    newsDetail();
  }
}

// hashÍ∞íÏù¥ Î≥ÄÍ≤ΩÎêòÎäî Í≤ΩÏö∞ : Î©îÎâ¥ Ïù¥Îèô , Í∏Ä ÎÇ¥Ïö© Ïù¥Îèô (2Í∞ÄÏßÄ() -> ÎùºÏö∞ÌÑ∞ÏóêÏÑú Ï≤òÎ¶¨
